.toggle-button {
  width: 40px;
  height: 20px;
  cursor: pointer;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background-color: #fff;
  border: .6px solid #fd5103;
  border-radius: 20px;
  outline: none;
  margin: 0;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.toggle-button:after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #fd5103;
  border-radius: 50%;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 1px;
  left: 1px;
  transform: translateX(0);
}

.toggle-button:checked:after {
  background-color: #fff;
  transform: translateX(calc(100% + 3px));
}

.toggle-button:checked {
  background-color: #fd5103;
  border: .6px solid #fff;
}

